<template>
  <div class="backColor">
    <div class="headerHistory backgroundNaviBar">
      <van-nav-bar
        title="历史数据"
        @click-left="()=>{
          $router.go(-1)
         }"
        left-arrow
        class="backgroundNaviBar"
        z-index="0"
        :border="false"
      >
        <!-- <van-icon name="search" slot="right" /> -->
      </van-nav-bar>
      <div class="backgroundNaviBar" id="showHeader">
        <div class="flexHistoryContainer">
          <div class="timeSelectHis">
            <span>筛选条件</span>
            <!-- <img style="margin-left:5px;" src="static/imgs/historyToBottom.png" alt /> -->
          </div>
          <div class="flexBoxHistory">
            <div @click="leftEchart()" :style="leftStyle" class="left">
              <span style="font-size:12px;">图表</span>
            </div>
            <div @click="rightTable()" :style="rightStyle" class="right">
              <span style="font-size:12px;">表格</span>
            </div>
          </div>
        </div>
        <div class="stationNameHistory">
          <div class="imageSize" id="selectStation">
            <!-- <div @click="selectDataPic">
            <span>站点名称</span>
            <img style="margin-left: 5px;" src="static/imgs/historyToBottom.png" alt />
            </div>-->
            <div class="flexBoxDayTime">
              <div @click="Day(0)" :style="one">当天</div>
              <div @click="Day(3)" :style="three">近三天</div>
              <div @click="Day(7)" :style="week">近一周</div>
              <div @click="Day(30)" :style="month">近一月</div>
            </div>
          </div>
          <div class="fontSizeStyle" @click="selectDataPic">
            <span>{{normal.value1}}</span>
          </div>
        </div>
        <div class="timeSelectHistory" @click="openBotttomSheet()">
          <div>
            <span>{{start}}</span> -
            <span>{{end}}</span>
          </div>
        </div>
      </div>
    </div>

    <div
      style="padding:10px; margin-top:224px;background: #faf8f8;"
      :style="margin"
      class="contanerMain"
      v-show="!showTable"
    >
      <div class="border-radius">
        <!-- <van-swipe indicator-color="orange" :show-indicators="false">
        <van-swipe-item class="swipeHeight">-->
        <div class="borderEchart border-radius">
          <div id="mainpH" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item v-if="$store.state.type==='false'"> -->
        <div class="borderEchart border-radius" id="showCod">
          <div id="maincod" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item> -->
        <div class="borderEchart border-radius">
          <div id="mainammoniaNitrogen" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item> -->
        <div class="borderEchart border-radius">
          <div id="mainnitrogen" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item> -->
        <div class="borderEchart border-radius">
          <div id="mainphosphorus" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item> -->
        <div class="borderEchart border-radius">
          <div id="maincuprum" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item> -->
        <div class="borderEchart border-radius">
          <div id="mainnickel" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item> -->
        <div class="borderEchart border-radius" id="showGe">
          <div id="mainchromium" class="echartHis"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- <van-swipe-item> -->
        <div class="borderEchart border-radius">
          <div id="mainprussiate" class="echartHis" style="margin-bottom: 0px;"></div>
        </div>
        <!-- </van-swipe-item> -->
        <!-- </van-swipe> -->
      </div>
    </div>
    <div>
      <div
        v-show="showTable"
        :style="margin"
        style="padding:10px;margin-top:224px;background: #faf8f8;"
      >
        <v-table ref="historyTable" />
      </div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-list-item>
            <div class="titleselect">
              <span>开始时间：</span>
            </div>
            <div class="select" @click="selectStartTime('start')">
              <input type="text" class="input" disabled v-model="value3" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>结束时间：</span>
            </div>
            <div class="select" @click="selectLastTime('end')">
              <input type="text" class="input" disabled v-model="value4" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btnBottom">
              <div class="left" @click="closeBottomSheet()">取消</div>
              <div
                class="right backgroundNaviBar"
                :style="'background:'+$store.state.color"
                @click="confirmData()"
              >确定</div>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <mu-container>
      <mu-bottom-sheet :open.sync="openTime">
        <mu-list @item-click="closeBottomSheet">
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            :min-date="minDate"
            @confirm="confirmDate"
            @cancel="closeTime"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <HistoryForData
      v-show="showOprate"
      :lasttime="value4"
      :starttime="value3"
      :waterstation="normal.value1"
    />
    <van-popup v-model="showStation" position="bottom" style="z-index:99999">
      <van-picker
        :columns="columns"
        show-toolbar
        :close-on-click-overlay="false"
        title="选择站点"
        :default-index="0"
        @cancel="closeSelect"
        @confirm="onChange"
      />
    </van-popup>

    <van-calendar
      v-model="showTimeRange"
      type="range"
      @confirm="onConfirm"
      :show-confirm="false"
      :min-date="minDate"
      :max-date="maxDate"
      :default-date="defaultTime"
      color="#328efb"
    />
    <!-- <div v-show="btnFlag" style="z-index:9999999;position:fixed;bottom:100px;">11111111111111</div> -->
  </div>
</template>
<script>
var echarts = require("echarts");
// 历史数据新改的表格
import Table from "@/views/historyTable/index";
import HistoryForData from "../../mapComponents/historyForData/index";
import AppBace from "../../../static/ajax";
import myTableData from "../../components/event/historyDataD";
import EchartsCommon from "../../../static/js/echartsCommon";
import HistoryData from "../../../static/js/History/historyData";

import HisEcharts from "../../../static/js/History/index";

import Vue from "vue";
import { Toast, Picker, Popup } from "vant";
import { Calendar } from "vant";

Vue.use(Calendar);
Vue.use(Toast)
  .use(Picker)
  .use(Popup);
// import Vue from 'vue';
// import { Picker } from 'vant';

Vue.use(Picker);
const h = document.documentElement.clientHeight || document.body.clientHeight;
const heightDevice = Math.floor((h - 249 - 40) / 42);
export default {
  name: "Assistant",
  data() {
    return {
      btnFlag: false,
      month: "border:none;color:rgb(219, 228, 247)",
      week: "border:none;color:rgb(219, 228, 247)",
      three: "border:none;color:rgb(219, 228, 247)",
      start: "",
      end: "",
      showOprate: false,
      imageListCasoul: [1, 2, 3, 4],
      status1: true,
      status2: true,
      msg: "",
      options: [],
      now: new Date(),
      value3:
        // this.$store.state.startTime
        AppBace.DateTime(0),
      value4: AppBace.DateTime(0),
      normal: {
        value1: ""
      },
      checkbox: {
        value1: [],
        value2: false,
        value3: false
      },
      checkboxContent: [
        { id: 1, content: "标液标准" },
        { id: 2, content: "标准曲线" },
        { id: 3, content: "精密度检查" }
      ],
      checkAll: true,
      count: 0,
      res: [],
      num: "",
      open: false,
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      openTime: false,
      showTime: "",
      title: "",
      factorStatus: "false",
      leftStyle: "",
      rightStyle: "border:none;color:#88affd",
      showTable: false,
      pageSize: heightDevice,
      showStation: false,
      columns: this.$store.state.menu,
      colorDay: "border:none;color:rgb(219, 228, 247)",
      one: "",
      startx: "",
      starty: "",
      margin: "",
      showTimeRange: false,
      minDate: new Date(2019, 0, 1),
      maxDate: new Date(),
      defaultTime: new Date(),
      showGe: true,
      showCod: true,
      hasCod: 0
    };
  },
  created() {
    var name = this.normal.value1;
    this.showFactor(name);
  },
  methods: {
    backTop() {
      const that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },

    // 为了计算距离顶部的高度，当高度大于60显示回顶部图标，小于60则隐藏
    // scrollToTop() {
    //   var scrollTop =
    //     window.pageYOffset ||
    //     document.documentElement.scrollTop ||
    //     document.body.scrollTop;
    //   console.log(scrollTop);
    //   const that = this;
    //   // let scrollTop =
    //   //   window.pageYOffset ||
    //   //   document.documentElement.scrollTop ||
    //   //   document.body.scrollTop;
    //   // that.scrollTop = scrollTop;
    //   // if (that.scrollTop > 60) {
    //   //   that.btnFlag = true;
    //   // } else {
    //   //   that.btnFlag = false;
    //   // }
    // },
    // cod显示
    showFactor(name) {
      if (
        name !== "沙井一期进水口" &&
        name !== "沙井二期进水口" &&
        name !== "福永水质净化厂前池" &&
        name !== "固戍水质净化厂" &&
        name !== "中途泵站"
      ) {
        // this.showGe = true;
        // this.showCod = false;
        $("#showCod").slideUp(200);
        $("#showGe").slideDown(200);
      } else if (name === "中途泵站") {
        // this.showCod = true;
        // this.showGe = true;
        // showGe
        $("#showCod").slideDown(200);
        $("#showGe").slideDown(200);
      } else {
        // this.showGe = false;
        // this.showCod = true;
        $("#showGe").slideUp(200);

        $("#showCod").slideDown(200);
      }
      console.log(11111111111);
    },

    onConfirm(val) {
      this.value3 = AppBace.filter(val[0]);
      this.value4 = AppBace.filter(val[1]);
      this.initData();
      this.showTimeRange = false;
    },
    closeSelect() {
      this.showStation = false;
      this.dataScoll();
    },
    Day(val) {
      this.value3 = AppBace.DateTime(val);
      this.value4 = AppBace.DateTime(0);
      this.$store.state.historyStatus = true;
      // debugger
      switch (val) {
        case 0:
          this.one = "";
          this.three = this.colorDay;
          this.month = this.colorDay;
          this.week = this.colorDay;
          break;
        case 3:
          this.one = this.colorDay;
          this.three = "";
          this.month = this.colorDay;
          this.week = this.colorDay;
          break;
        case 7:
          this.one = this.colorDay;
          this.three = this.colorDay;
          this.month = this.colorDay;
          this.week = "";
          break;
        case 30:
          this.one = this.colorDay;
          this.three = this.colorDay;
          this.month = "";
          this.week = this.colorDay;
          break;
        // rgb(88, 136, 230)

        default:
          this.one = this.colorDay;
          this.three = this.colorDay;
          this.month = this.colorDay;
          this.week = this.colorDay;
          break;
      }
      // this.backtop();
      this.initData();
    },
    async onChange(val) {
      this.showStation = false;
      // this.showGe = true;
      // this.showCod = true;
      this.normal.value1 = val;
      var name = val;
      console.log(val);
      this.dataScoll();
      await this.initData(val);
      await this.showFactor(name);
    },
    leftEchart() {
      this.initData();
      this.rightStyle = "border:none;color:#88affd";
      this.leftStyle = "";
      this.showTable = false;
    },
    rightTable() {
      this.leftStyle = "border:none;color:#88affd";
      this.rightStyle = "";
      this.showTable = true;
    },
    confirmDate(value) {
      this.openTime = false;
      switch (this.showTime) {
        case "start":
          this.value3 = AppBace.filter(value);
          break;

        case "end":
          this.value4 = AppBace.filter(value);
          break;
      }
    },
    closeTime() {
      this.openTime = false;
    },
    selectDataPic() {
      this.open = false;
      var _this = this;
      this.showStation = true;
      window.removeEventListener("touchstart", this.startFun, true);
      window.removeEventListener("touchend", this.endFunc, true);
    },
    selectStartTime(e) {
      this.openTime = true;
      this.showTime = e;
    },
    selectLastTime(e) {
      this.openTime = true;
      this.showTime = e;
    },
    confirmData() {
      this.open = false;
      setTimeout(() => {
        this.initData(null);
      }, 100);
    },
    openBotttomSheet() {
      this.showTimeRange = true;
      // this.Day(0);
    },
    closeBottomSheet() {
      this.open = false;
    },
    async initData(e) {
      var waterstation = "";
      if (e !== null && e !== undefined) {
        waterstation = this.formenu(e);
      } else {
        waterstation = this.normal.value1;
        this.formenu(waterstation);
      }
      this.title = waterstation;
      var day = 0;
      var advice = "";
      var url = "/station/water-data/form";
      //  参数：starttime/waterstation/lasttime

      var starttime = this.value3;
      var lasttime = this.value4;
      var isExcel = "no";
      var pageNum = null;
      var pageSize = null;
      day = AppBace.dateTimeDev(starttime, lasttime);
      var res = await AppBace.request(
        url,
        "POST",
        {
          starttime,
          lasttime,
          waterstation,
          pageNum,
          pageSize,
          isExcel
        },
        this.token
      );
      // debugger
      this.start = AppBace.filterDatePoint(this.value3);
      this.end = AppBace.filterDatePoint(this.value4);
      this.res = res;
      this.hasCod = res.data.hasCod;
      console.log("form", res);
      // this.backtop();
      // setTimeout(() => {
      HisEcharts.initHistoryDataEchartsPH(
        res.data.data,
        this.$store.state.type,
        res.data.hasCod
      );
      // }, 50);
      // this.initTest(arraypH, arrayCu, arraygmtMonitor);
      this.$refs.historyTable.initData(starttime, lasttime, this.normal.value1);

      // this.resData();
      if (day === 0) {
        day = "24小时";
      } else if (day < 0) {
        Toast("时间筛选有误，请重新选择");
        return;
      } else {
        day = day + "天";
      }
      if (res.data.data.length === 0) {
        advice = ",当前水站信息数据为空，请调整区间或者查看其他站点";
      }
      Toast("数据为最近" + day + "的历史数据" + advice);
    },
    imageListCasoulClik(index) {
      switch (index) {
        case 0:
          this.status1 = true;
          this.status2 = false;
          break;

        default:
          this.status1 = false;
          this.status2 = true;
          break;
      }
    },
    formenu(e) {
      var stationArr = this.$store.state.menu;
      for (let index = 0; index < stationArr.length; index++) {
        var element = stationArr[index];
        if (e === element.slice(0, 7)) {
          return stationArr[index];
        }
      }
    },
    dataScoll() {
      // var startx, starty;
      //获得角度

      //根据起点终点返回方向 1向上 2向下 3向左 4向右 0未滑动

      //手指接触屏幕
      window.addEventListener("touchstart", this.startFun, true);
      //手指离开屏幕
      window.addEventListener("touchend", this.endFunc, true);
    },
    getAngle(angx, angy) {
      return (Math.atan2(angy, angx) * 180) / Math.PI;
    },
    startFun(e) {
      this.startx = e.touches[0].pageX;
      this.starty = e.touches[0].pageY;
    },
    endFunc(e) {
      var endx, endy;
      endx = e.changedTouches[0].pageX;
      endy = e.changedTouches[0].pageY;
      var direction = this.getDirection(this.startx, this.starty, endx, endy);
      switch (direction) {
        case 0:
          console.log("未滑动！");
          break;
        case 1:
          // console.log("向上！");
          // this.showHeader = false;
          this.margin = "margin-top:50px";
          // this.showHeaderStyle =
          // "showHeaderStyle:opacity: 0;filter:Alpha(opacity=0); transition: opacity 2s;";
          $("#showHeader").slideUp(200);
          //  $("#showHeader").slideToggle(200);

          break;
        case 2:
          // console.log("向下！");
          // this.showHeader = true;
          //  $("#showHeader").slideToggle(200);
          $("#showHeader").slideDown(200);
          this.margin = "margin-top:224px";
          break;
        case 3:
          console.log("向左！");
          this.leftEchart();
          break;
        case 4:
          console.log("向右！");
          this.rightTable();
          break;
        default:
      }
    },
    getDirection(startx, starty, endx, endy) {
      var angx = endx - startx;
      var angy = endy - starty;
      var result = 0;

      //如果滑动距离太短
      if (Math.abs(angx) < 2 && Math.abs(angy) < 2) {
        return result;
      }

      var angle = this.getAngle(angx, angy);
      if (angle >= -135 && angle <= -45) {
        result = 1;
      } else if (angle > 45 && angle < 135) {
        result = 2;
      } else if (
        (angle >= 135 && angle <= 180) ||
        (angle >= -180 && angle < -135)
      ) {
        result = 3;
      } else if (angle >= -45 && angle <= 45) {
        result = 4;
      }

      return result;
    }
  },
  async mounted() {
    //  window.addEventListener('scroll', this.scrollToTop,true);
    $("#showCod").slideUp(200);

    this.hasCod = 0;
    window.addEventListener("touchstart", this.startFun, true);
    //手指离开屏幕
    window.addEventListener("touchend", this.endFunc, true);
    // this.options = this.$store.state.menu;
    var user = sessionStorage.getItem("user");
    this.token = sessionStorage.getItem("token");
    if (user === "operator" || user === "admin") {
      this.showOprate = true;
    }
    var warterStation = "";

    if (
      this.$route.query.stationName !== "" &&
      this.$route.query.stationName !== undefined &&
      this.$route.query.stationName !== null
    ) {
      warterStation = this.$route.query.stationName;
    } else {
      warterStation = this.$store.state.menu[0];
    }

    this.title = warterStation;
    this.normal.value1 = warterStation;
    await this.initData(warterStation);
    this.status2 = false;
    console.log(heightDevice);
    this.rightTable();
  },
  // mounted(){},
  destroyed() {
    window.removeEventListener("touchstart", this.startFun, true);
    window.removeEventListener("touchend", this.endFunc, true);
    // window.removeEventListener('scroll', this.scrollToTop,true);
  },
  components: {
    myTableData,
    HistoryForData,
    "v-table": Table
  }
};
</script>
