<script>
import AppBace from "../../../static/ajax";
import ChangeColor from "../../../static/js/changeColor/index";
import { limitFlag } from "../../../static/limitFlag";
export default {
  data() {
    return {
      itemContent: [1, 1, 1, 1, 1, 1, 1, 1],
      num: 10,
      refreshing: false,
      loading: false,
      token: "",
      showCod: false,
      user: "",
      green: "color:green",
      hasCode: 0,
      red: "color:red",
      huanbao: false,
      orange: "color:orange",
      first: true,
      dayTime: []
    };
  },
  filters: {
    filter(value) {
      //debugger
      //console.log("====",value);

      if (value !== null) {
        if (typeof value == "number") {
          return value.toFixed(3);
        } else {
          return value;
        }
      } else {
        return "--";
      }
    },
    // 因子的显示内容
    filterNews(value, user, water, device, name) {
      //debugger
      //console.log("====",value);
      // 无水跟device的优先级？
      if (water === "无水") {
        return "无水";
      } else {
        if (user == true && device == 4) {
          return "无水";
        } else {
          if (device === 4) {
            return limitFlag[name];
          }
          if (device === 5) {
            return "--";
          }
          // if (user == true) {
          if (value !== null) {
            if (typeof value == "number") {
              return value.toFixed(3);
            } else {
              return value;
            }
          } else {
            return "--";
          }
          // }
        }
      }
    },
    // 不用判断微超标的几个因子
    filterWater(value, standed, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value > standed) {
          return (color = "color:red");
        } else {
          return "";
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
       return color = "color:red";
      } else {
        if (value > standed) {
          return (color = "color:red");
        } else {
          return "";
        }
      }
    },
    // 判断微超标的颜色
    filterYellowtToStyle(value, standed, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value > standed && value < standed * 1.15) {
          return (color = "color:orange");
        } else if (value > standed * 1.15) {
          return (color = "color:red");
        } else {
          return "";
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
       return color = "color:red";
      } else {
        if (value > standed && value < standed * 1.15) {
          return (color = "color:orange");
        } else if (value > standed * 1.15) {
          return (color = "color:red");
        } else {
          return "";
        }
      }
    },
    // 判断ph的颜色
    filterPhStyle(value, small, large, water) {
      var color = "";
      if (
        water !== "无水" &&
        water !== "超标" &&
        water !== "黄色" &&
        water !== "-"
      ) {
        if (value!==null) {
          if (value < small || value > large) {
          return (color = "color:red");
        } else {
          return "";
        }
        }else{
          return ""
        }
      } else if (water === "-") {
        return (color = "");
      } else if (water === "无水") {
       return color = "color:red";
      } else {
       if (value!==null) {
          if (value < small || value > large) {
          return (color = "color:red");
        } else {
          return "";
        }
       }else{
         return "--"
       }
      }
    },
  },
  methods: {
    backTop() {
      const that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },
    refresh() {
      this.refreshing = true;
      this.$refs.container.scrollTop = 0;
      // setTimeout(() => {
      //   this.refreshing = false;
      //   this.text = this.text === "List" ? "Menu" : "List";
      this.num = 10;
      this.initData(
        this.$store.state.historyData.starttime,
        this.$store.state.historyData.lasttime,
        this.$store.state.historyData.waterstation
      );
      // }, 2000);
    },
    load() {
      this.loading = true;
      // setTimeout(() => {
      //   this.loading = false;
      this.num += 5;
      this.initData(
        this.$store.state.historyData.starttime,
        this.$store.state.historyData.lasttime,
        this.$store.state.historyData.waterstation
      );
      // }, 2000);
    },
    async initData(starttime1, lasttime1, station1) {
      if (this.$store.state.historyStatus === true) {
        this.itemContent = [];
        this.$store.state.historyStatus =false;
      }
      var day = "";

      // if (this.first == false) {
      //   day = AppBace.dateTimeDev(starttime1, lasttime1);
      //   this.dayTime.push(day);
      //   // if (AppBace.dateTimeDev(starttime1,lasttime1)==) {

      //   // }
      // } else {
      //   day = AppBace.dateTimeDev(starttime1, lasttime1);
      //   this.dayTime.push(day);
      //   this.first = true;
      //   if (this.dayTime.length >= 2) {
      //     if (this.dayTime[0] == this.dayTime[1]) {
      //       this.num = 10;
      //     }
      //   }
      // }
      var url = "/station/water-data/list";
      this.$store.state.historyData.starttime = starttime1;
      this.$store.state.historyData.lasttime = lasttime1;
      this.$store.state.historyData.waterstation = station1;
      var waterstation = station1;
      var starttime = starttime1;
      var lasttime = lasttime1;
      // debugger
      var isExcel = "no";
      var pageNum = 1;
      var pageSize = this.num;
      // console.log(this.pageSize);
      var res = await AppBace.request(
        url,
        "POST",
        {
          starttime,
          lasttime,
          waterstation,
          isExcel,
          pageNum,
          pageSize
        },
        this.token
      );
      this.loading = false;
      // this.backTop();
      this.refreshing = false;
      console.log("history", res);
      this.hasCode = res.data.hasCod;
      console.log(this.hasCode);
      this.itemContent = res.data.data;
    }
    // scrollToTop() {
    //   var scrollTop =
    //     window.pageYOffset ||
    //     document.documentElement.scrollTop ||
    //     document.body.scrollTop;
    //   console.log(scrollTop);
    //   const that = this;
    //   // let scrollTop =
    //   //   window.pageYOffset ||
    //   //   document.documentElement.scrollTop ||
    //   //   document.body.scrollTop;
    //   // that.scrollTop = scrollTop;
    //   // if (that.scrollTop > 60) {
    //   //   that.btnFlag = true;
    //   // } else {
    //   //   that.btnFlag = false;
    //   // }
    // }
    //
  },
  created() {
    this.token = sessionStorage.getItem("token");
    this.user = sessionStorage.getItem("user");
    if (this.user === "manager") {
      this.huanbao = true;
    }
  },
  mounted() {
    // window.addEventListener("scroll", this.scrollToTop);
  },
  destroyed() {
    // window.removeEventListener("scroll", this.scrollToTop);
  }
};
</script>
